import React from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import Input from '../../components/Input/input'
import {ReactComponent as SearchSvg} from '../svg/search.svg'
import Button from "../../components/Button/button";
const columns = [
  {
    title: '',
    dataIndex: 'Attribute',
    key: 'Attribute',

  },
  {
    title: '',
    dataIndex: 'Description',
    key: 'Description'
  },
  {
    title: '',
    dataIndex: 'Type',
    key: 'Type'
  },{
    title: '',
    dataIndex: 'Default',
    key: 'Default'
  }
];
const dataSource =  [
  {
    key: '1',
    attribute: 'defaultValue',
    description: '',
    type: `string`,
    default: ``
  },
  {
    key: '2',
    attribute: 'value',
    description: '',
    type: `string`,
    default: ``,
  },
  {
    key: '3',
    attribute: 'placeholder',
    description: '',
    type: 'string',
    default: '',
  },
  {
    key: '4',
    attribute: 'disabled',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '5',
    attribute: 'error',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '6',
    attribute: 'thin',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '7',
    attribute: 'readonly',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '8',
    attribute: 'onChange',
    description: '',
    type: 'React.ChangeEventHandler',
    default: '',
  },
  {
    key: '9',
    attribute: 'onKeyDown',
    description: '',
    type: 'React.KeyboardEventHandler',
    default: '',
  },
  {
    key: '10',
    attribute: 'onPressEnter',
    description: 'Enter ',
    type: 'React.KeyboardEventHandler',
    default: '',
  },
  {
    key: '11',
    attribute: 'onFocus',
    description: '',
    type: 'React.FocusEventHandler',
    default: '',
  },
  {
    key: '12',
    attribute: 'onBlur',
    description: '',
    type: 'React.FocusEventHandler',
    default: '',
  },
  {
    key: '13',
    attribute: 'addonBefore',
    description: '',
    type: 'string | ReactElement',
    default: '',
  },
  {
    key: '14',
    attribute: 'addonAfter',
    description: '',
    type: 'string | ReactElement',
    default: '',
  },
  {
    key: '15',
    attribute: 'prefix',
    description: '',
    type: 'string | ReactElement',
    default: '',
  },
  {
    key: '16',
    attribute: 'suffix',
    description: '',
    type: 'string | ReactElement',
    default: '',
  },
  {
    key: '17',
    attribute: 'style',
    description: ' Button ',
    type: 'React.CSSProperties',
    default: '',
  },
  {
    key: '18',
    attribute: 'className',
    description: ' Button ',
    type: 'string',
    default: '',
  },
]
const Code1 = `<Input placeholder={''}/>
`
const Code2 = `<Input placeholder={'Error'} error={true}/>

<Input placeholder={'disabled'} disabled={true}/>       
`
const Code3 = `<Input placeholder={'google'} addonBefore={'http://'} addonAfter={'.com'}/>

<Input placeholder={''} addonBefore={''}/>
`
const Code4 = `<Input placeholder={''} addonAfter={searchSvg}/>
`
const Code5 = `<Input placeholder={''} addonAfter={searchButton}/>
`
const Code6 = `<Input placeholder={'mini'} inputSize={"mini"}/>

<Input placeholder={'thin'} inputSize={"thin"}/>

<Input placeholder={'strong'}/>
`
const InputDisplay = () => {

  const searchSvg = (
    <div style={{margin: '0 20px'}}>
      <SearchSvg/>
    </div>
  )
  const searchButton = <Button btnType={'success'} size={'tiny'}></Button>
  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>Input </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code1}
          >
            <div className="flex-column-wrapper">
              <Input placeholder={''}/>
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code2}
          >
            <div className="flex-column-wrapper">
              <div className={'padding-bottom-small'}>
                <Input placeholder={'Error'} error={true}/>
              </div>
              <Input placeholder={'disabled'} disabled={true}/>
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={'/'}
              explain={''}
              code={Code3}
          >
            <div className="flex-column-wrapper">
              <div className={'padding-bottom-small'}>
                <Input placeholder={'google'} addonBefore={'http://'} addonAfter={'.com'}/>
              </div>
              <Input placeholder={''} addonBefore={''}/>
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={'/'}
              explain={''}
              code={Code4}
          >
            <div className="flex-column-wrapper">
              <Input placeholder={''} addonAfter={searchSvg}/>
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={'/'}
              explain={''}
              code={Code5}
          >
            <div className="flex-column-wrapper">
              <Input placeholder={''} addonAfter={searchButton}/>
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code6}
          >
            <div className="flex-column-wrapper">
              <div className={'padding-bottom-small'}>
                <Input placeholder={'mini'} inputSize={"mini"}/>
              </div>
              <div className={'padding-bottom-small'}>
                <Input placeholder={'thin'} inputSize={"thin"}/>
              </div>
              <Input placeholder={'strong'}/>
            </div>
          </Card>
        </div>

        <h3 className={'display-tittle'}>API</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource}/>
        </div>

      </div>
  )
}

export default InputDisplay
