import React from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import RadioGroup from "../../components/Radio/radioGroup";
import Radio from "../../components/Radio/radio";
const columns = [
  {
    title: '',
    dataIndex: 'Attribute',
    key: 'Attribute',

  },
  {
    title: '',
    dataIndex: 'Description',
    key: 'Description'
  },
  {
    title: '',
    dataIndex: 'Type',
    key: 'Type'
  },{
    title: '',
    dataIndex: 'Default',
    key: 'Default'
  }
];
const dataSource =  [
  {
    key: '1',
    attribute: 'defaultValue',
    description: '',
    type: `string`,
    default: ``,
  },
  {
    key: '2',
    attribute: 'value',
    description: '',
    type: `string`,
    default: ``,
  },
  {
    key: '3',
    attribute: 'vertical',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '4',
    attribute: 'radioStyle',
    description: '',
    type: `'button' | 'radio'`,
    default: `'radio'`,
  },
  {
    key: '5',
    attribute: 'onChange',
    description: '',
    type: '(value: string) => void',
    default: '',
  }
]
const dataSource2 =  [
  {
    key: '1',
    attribute: 'value',
    description: '',
    type: `string`,
    default: ``,
  },
  {
    key: '2',
    attribute: 'index',
    description: '',
    type: `string`,
    default: ``,
  }
]
const Code1 = `<RadioGroup>
  <Radio value={''}></Radio>
  <Radio value={''}></Radio>
  <Radio value={''}></Radio>
  <Radio value={''}></Radio>
</RadioGroup>
`
const Code2 = `<RadioGroup vertical={true}>
  <Radio disabled={true} value={''}></Radio>
  <Radio value={''}></Radio>
  <Radio value={''}></Radio>
  <Radio value={''}></Radio>
</RadioGroup>
`
const Code3 = `<RadioGroup radioStyle={'button'}>
  <Radio value={''}></Radio>
  <Radio value={''}></Radio>
  <Radio value={''}></Radio>
  <Radio value={''}></Radio>
</RadioGroup>
`
const Code4 = `<RadioGroup radioStyle={'button'} vertical={true}>
  <Radio disabled={true} value={''}></Radio>
  <Radio value={''}></Radio>
  <Radio value={''}></Radio>
  <Radio value={''}></Radio>
</RadioGroup>
`
const RadioDisplay = () => {

  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>Radio </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code1}
          >
            <RadioGroup>
              <Radio value={''}></Radio>
              <Radio value={''}></Radio>
              <Radio value={''}></Radio>
              <Radio value={''}></Radio>
            </RadioGroup>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={'/'}
              explain={''}
              code={Code2}
          >
            <RadioGroup vertical={true}>
              <Radio disabled={true} value={''}></Radio>
              <Radio value={''}></Radio>
              <Radio value={''}></Radio>
              <Radio value={''}></Radio>
            </RadioGroup>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code3}
          >
            <RadioGroup radioStyle={'button'}>
              <Radio value={''}></Radio>
              <Radio value={''}></Radio>
              <Radio value={''}></Radio>
              <Radio value={''}></Radio>
            </RadioGroup>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={'/'}
              explain={''}
              code={Code4}
          >
            <div className="flex-column-wrapper">
              <RadioGroup radioStyle={'button'} vertical={true}>
                <Radio disabled={true} value={''}></Radio>
                <Radio value={''}></Radio>
                <Radio value={''}></Radio>
                <Radio value={''}></Radio>
              </RadioGroup>
            </div>
          </Card>
        </div>

        <h3 className={'display-tittle'}>API</h3>
        <h3 className={'display-tittle'}>RadioGroup</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource}/>
        </div>
        <h3 className={'display-tittle'}>Radio</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource2}/>
        </div>

      </div>
  )
}

export default RadioDisplay
