import React from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import Upload from "../../components/Upload/upload";
import Button from "../../components/Button/button";
import {ReactComponent as FileSvg} from "../svg/File.svg";

const columns = [
  {
    title: '',
    dataIndex: 'Attribute',
    key: 'Attribute',

  },
  {
    title: '',
    dataIndex: 'Description',
    key: 'Description'
  },
  {
    title: '',
    dataIndex: 'Type',
    key: 'Type'
  },{
    title: '',
    dataIndex: 'Default',
    key: 'Default'
  }
];
const dataSource =  [
  {
    key: '1',
    attribute: 'action',
    description: '',
    type: `string`,
    default: ``,
  },
  {
    key: '2',
    attribute: 'defaultFileList',
    description: '',
    type: `UploadFile[]`,
    default: ``,
  },
  {
    key: '3',
    attribute: 'beforeUpload',
    description: ' false  Promise ',
    type: `(file: File) => boolean | Promise`,
    default: '',
  },
  {
    key: '4',
    attribute: 'onProgress',
    description: '',
    type: '(percentage: number, file: UploadFile) => void',
    default: '',
  },
  {
    key: '5',
    attribute: 'onSuccess',
    description: '',
    type: '(data: any, file: UploadFile) => void',
    default: '',
  },
  {
    key: '6',
    attribute: 'onError',
    description: '',
    type: '(err: any, file: UploadFile) => void',
    default: '',
  },
  {
    key: '7',
    attribute: 'onChange',
    description: '',
    type: '(file: UploadFile) => void',
    default: '',
  },
  {
    key: '8',
    attribute: 'onRemove',
    description: '',
    type: '(file: UploadFile) => void',
    default: '',
  },
  {
    key: '9',
    attribute: 'headers',
    description: '',
    type: '{ [key: string]: any; }',
    default: '',
  },
  {
    key: '10',
    attribute: 'name',
    description: '',
    type: 'string',
    default: 'files',
  },
  {
    key: '11',
    attribute: 'data',
    description: '',
    type: '{ [key: string]: any; }',
    default: 'files',
  },
  {
    key: '12',
    attribute: 'withCredentials',
    description: ' cookie ',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '13',
    attribute: 'accept',
    description: ', ',
    type: 'string',
    default: '',
  },
  {
    key: '14',
    attribute: 'multiple',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '15',
    attribute: 'drag',
    description: '',
    type: 'boolean',
    default: 'false',
  },

]
const Code1 = `<Upload
    action="https://run.mocky.io/v3/0fe6d8b9-3d95-4d40-a889-02709b88a7e3"
    name="file"
    onChange={function noRefCheck(){}}
    onProgress={function noRefCheck(){}}
    onRemove={function noRefCheck(){}}
    onSuccess={function noRefCheck(){}}
>
  <Button
      btnType="primary"
      disabled={false}
  >
    
  </Button>
</Upload>
`
const Code2 = `<Upload
    action="https://run.mocky.io/v3/0fe6d8b9-3d95-4d40-a889-02709b88a7e3"
    drag
    multiple
    name="fileName"
    onChange={function noRefCheck(){}}
    onRemove={function noRefCheck(){}}
>
  <FileSvg/>
  <br />
  <p>
    
    <br />
    
  </p>
</Upload>
`

const UploadDisplay = () => {
  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>Upload </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={'/'}
              explain={''}
              code={Code1}
          >
            <Upload
                action="https://run.mocky.io/v3/0fe6d8b9-3d95-4d40-a889-02709b88a7e3"
                name="file"
                onChange={function noRefCheck(){}}
                onProgress={function noRefCheck(){}}
                onRemove={function noRefCheck(){}}
                onSuccess={function noRefCheck(){}}
            >
              <Button
                  btnType="primary"
                  disabled={false}
              >
                
              </Button>
            </Upload>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={'.'}
              code={Code2}
          >
            <Upload
                action="https://run.mocky.io/v3/0fe6d8b9-3d95-4d40-a889-02709b88a7e3"
                drag
                multiple
                name="fileName"
                onChange={function noRefCheck(){}}
                onRemove={function noRefCheck(){}}
            >
              <FileSvg/>
              <br />
              <p>
                
                <br />
                
              </p>
            </Upload>
          </Card>
        </div>
        <h3 className={'display-tittle'}>API</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource}/>
        </div>

      </div>
  )
}

export default UploadDisplay
