import React from 'react'
import {ReactComponent as ComponentsSvg} from './svg/components.svg'
import {ReactComponent as BalloonSvg} from './svg/balloon.svg'
import {Link} from 'react-router-dom'
const HomeSection = () => {
  return (
      <div className={'home-section'}>
        <Link to={'/'}>
          <div className={'home-section__card'}>
            <div className="image">
              <BalloonSvg/>
            </div>
            <h4 className={'tittle'}></h4>
            <p className={'info'}></p>
          </div>
        </Link>
        <Link to={'/components'}>
        <div className={'home-section__card'}>
          <div className="image">
            <ComponentsSvg/>
          </div>
          <h4 className={'tittle'}></h4>
          <p className={'info'}></p>
        </div>
        </Link>
        <div className={'home-section__card'}>
          <div className="image">
            <div>{`/`}</div>
            <div>{`/`}</div>
            <div>{``}</div>
            <div>{``}</div>
          </div>
          <h4 className={'tittle'}>Benefit Admin</h4>
          <p className={'info'}></p>
        </div>
      </div>
  )
}

export default HomeSection
