import React, {useState} from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import Button from '../../components/Button/button'
const columns = [
    {
      title: '',
      dataIndex: 'Attribute',
      key: 'Attribute',

    },
    {
      title: '',
      dataIndex: 'Description',
      key: 'Description'
    },
    {
      title: '',
      dataIndex: 'Type',
      key: 'Type'
    },{
      title: '',
      dataIndex: 'Default',
      key: 'Default'
    }
  ];
  const dataSource =  [
    {
      key: '1',
      attribute: 'dataSource',
      description: '',
      type: `DataSourceProps[]`,
      default: ``,
    },
    {
      key: '2',
      attribute: 'columns',
      description: '',
      type: `ColumnProps[]`,
      default: ``,
    },
    {
      key: '3',
      attribute: 'rowSelection',
      description: 'CheckBox',
      type: 'boolean',
      default: 'false',
    },
    {
      key: '4',
      attribute: 'onSelectionChange',
      description: '',
      type: '(selection: string[]) => void',
      default: '',
    },
    {
      key: '5',
      attribute: 'isLoading',
      description: '',
      type: 'boolean',
      default: 'false',
    },
    {
      key: '6',
      attribute: 'edit',
      description: '',
      type: 'boolean',
      default: 'false',
    }
  ]
const dataSource2 =  [
  {
    key: '1',
    attribute: 'title',
    description: '',
    type: `string`,
    default: ``,
  },
  {
    key: '2',
    attribute: 'key',
    description: '',
    type: `string`,
    default: ``,
  },
  {
    key: '3',
    attribute: 'render',
    description: '',
    type: `(...resProps: any[]) => React.ReactNode`,
    default: '',
  },
  {
    key: '4',
    attribute: 'sort',
    description: '',
    type: 'boolean',
    default: 'false',
  }
]
  const sortTableColumns = [
    {
      title: '',
      dataIndex: 'Date',
      key: 'Date',
      sort:true
    },
    {
      title: '',
      dataIndex: 'Name',
      key: 'Name'
    },
    {
      title: '',
      dataIndex: 'Address',
      key: 'Address'
    }
  ];
  const baseTableColumns = [
    {
      title: '',
      dataIndex: 'Date',
      key: 'Date',

    },
    {
      title: '',
      dataIndex: 'Name',
      key: 'Name'
    },
    {
      title: '',
      dataIndex: 'Address',
      key: 'Address'
    }
  ];
const customTableColumns = [
  {
    title: '',
    dataIndex: 'Date',
    key: 'Date',

  },
  {
    title: '',
    dataIndex: 'Name',
    key: 'Name'
  },
  {
    title: '',
    dataIndex: 'Address',
    key: 'Address'
  },
  {
    title: '',
    dataIndex: 'custom',
    key: 'Custom',
    render: () => {
      return (
          <Button btnType={"none"} size={"tiny"}></Button>
      )
    }
  }
];

  const baseTableData = [{
    key: '1',
    date: '2016-05-02',
    name: '',
    address: ' 1518 '
  }, {
    key: '2',
    date: '2016-05-04',
    name: '',
    address: ' 1517 '
  }, {
    key: '3',
    date: '2016-05-01',
    name: '',
    address: ' 1519 '
  }, {
    key: '4',
    date: '2016-05-03',
    name: '',
    address: ' 1516 '
  }]
const Code1 = `const baseTableColumns = [
  {
    title: '',
    dataIndex: 'Date',
    key: 'Date',

  },
  {
    title: '',
    dataIndex: 'Name',
    key: 'Name'
  },
  {
    title: '',
    dataIndex: 'Address',
    key: 'Address'
  }
]

const baseTableData = [{
  key: '1',
  date: '2016-05-02',
  name: '',
  address: ' 1518 '
}, {
  key: '2',
  date: '2016-05-04',
  name: '',
  address: ' 1517 '
}, {
  key: '3',
  date: '2016-05-01',
  name: '',
  address: ' 1519 '
}, {
  key: '4',
  date: '2016-05-03',
  name: '',
  address: ' 1516 '
}]

<Table dataSource={baseTableData} columns={baseTableColumns}/>
`
const Code2 = `<Table 
  rowSelection={true} 
  dataSource={baseTableData} 
  columns={baseTableColumns}
/>
`
const Code3 = `const sortTableColumns = [
  {
    title: '',
    dataIndex: 'Date',
    key: 'Date',
    sort:true
  },
  {
    title: '',
    dataIndex: 'Name',
    key: 'Name'
  },
  {
    title: '',
    dataIndex: 'Address',
    key: 'Address'
  }
]
<Table 
  rowSelection={true} 
  dataSource={baseTableData} 
  columns={sortTableColumns}
/>
`
const Code4 = `<Table 
  isLoading={loading} 
  rowSelection={true} 
  dataSource={baseTableData} 
  columns={sortTableColumns}
/>
`
const Code5 = `<Table 
  rowSelection={true} 
  edit={true} 
  dataSource={baseTableData} 
  columns={baseTableColumns}
/>
`
const Code6 = `const customTableColumns = [
  {
    title: '',
    dataIndex: 'Date',
    key: 'Date',

  },
  {
    title: '',
    dataIndex: 'Name',
    key: 'Name'
  },
  {
    title: '',
    dataIndex: 'Address',
    key: 'Address'
  },
  {
    title: '',
    dataIndex: 'custom',
    key: 'Custom',
    render: () => {
      return (
          <Button btnType={"none"} size={"tiny"}></Button>
      )
    }
  }
]
<Table 
  rowSelection={true} 
  dataSource={baseTableData} 
  columns={customTableColumns}
/>
`
const TableDisplay = () => {
  const [loading, setLoading] = useState(true)
  const handleClickLoading = () => {
    setLoading(!loading)
  }

  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>Table </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code1}
          >
            <div style={{width: '100%'}}>
              <Table dataSource={baseTableData} columns={baseTableColumns}/>
            </div>

          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code2}
          >
            <div style={{width: '100%'}}>
              <Table rowSelection={true} dataSource={baseTableData} columns={baseTableColumns}/>
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code3}
          >
            <div style={{width: '100%'}}>
              <Table rowSelection={true} dataSource={baseTableData} columns={sortTableColumns}/>
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code4}
          >
            <div style={{width: '100%'}} className="flex-column-wrapper">
              <div style={{width: '100%'}} className="padding-bottom-small">
              <Table isLoading={loading} rowSelection={true} dataSource={baseTableData} columns={sortTableColumns}/>
            </div>
            <div>
              <Button onClick={handleClickLoading} size={'tiny'}></Button>
            </div>

            </div>

          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code5}
          >
          <div style={{width: '100%'}}>
              <Table rowSelection={true} edit={true} dataSource={baseTableData} columns={baseTableColumns}/>
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code6}
          >
            <div style={{width: '100%'}}>
              <Table rowSelection={true} dataSource={baseTableData} columns={customTableColumns}/>
            </div>
          </Card>
        </div>
        <h3 className={'display-tittle'}>API</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource}/>
        </div>
        <h3 className={'display-tittle'}>ColumnProps</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource2}/>
        </div>

      </div>
  )
}

export default TableDisplay
