import React, {useState} from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import Button from "../../components/Button/button";
import CheckBox from "../../components/CheckBox/checkBox";
import CheckBoxGroupNext from "../../components/CheckBox/checkBoxGroupNext";
const columns = [
  {
    title: '',
    dataIndex: 'Attribute',
    key: 'Attribute',

  },
  {
    title: '',
    dataIndex: 'Description',
    key: 'Description'
  },
  {
    title: '',
    dataIndex: 'Type',
    key: 'Type'
  },{
    title: '',
    dataIndex: 'Default',
    key: 'Default'
  }
];
const dataSource =  [
  {
    key: '1',
    attribute: 'defaultChecked',
    description: '',
    type: `boolean`,
    default: `false`,
  },
  {
    key: '2',
    attribute: 'checked',
    description: '',
    type: `boolean`,
    default: `false`,
  },
  {
    key: '3',
    attribute: 'disabled',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '4',
    attribute: 'indeterminate',
    description: ' indeterminate ',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '5',
    attribute: 'onChange',
    description: '',
    type: 'function(e:Event)',
    default: '',
  }
]
const dataSource2 =  [
  {
    key: '1',
    attribute: 'options',
    description: '',
    type: `Option[]`,
    default: `[]`,
  },
  {
    key: '2',
    attribute: 'defaultValue',
    description: '',
    type: `string[]`,
    default: `[]`,
  },
  {
    key: '3',
    attribute: 'tittle',
    description: '',
    type: 'string',
    default: '',
  },
  {
    key: '4',
    attribute: 'onChange',
    description: '',
    type: '(value: string[]) => any',
    default: '',
  },
  {
    key: '5',
    attribute: 'className',
    description: ' CheckBoxGroup ',
    type: 'string',
    default: '',
  },
  {
    key: '6',
    attribute: 'style',
    description: ' CheckBoxGroup ',
    type: 'React.CSSProperties',
    default: '',
  }
]
const optionDataSpurce = [
  {
    key: '1',
    attribute: 'value',
    description: 'CheckBox ',
    type: 'string',
    default: '',
  },
  {
    key: '2',
    attribute: 'label',
    description: '',
    type: 'string',
    default: '',
  },
  {
    key: '3',
    attribute: 'disabled',
    description: '',
    type: 'boolean',
    default: '',
  }
]
const group = [
  {
    value: '',
    label: ''
  },
  {
    value: '',
    label: ''
  },
  {
    value: '',
    label: ''
  },
  {
    value: '',
    label: ''
  }
]
const Code1 = `<CheckBox defaultChecked={true}></CheckBox>
`
const Code2 = `<CheckBox checked={true}></CheckBox>
<CheckBox indeterminate={true}></CheckBox>
<CheckBox checked={true} disabled={true}></CheckBox>
<CheckBox disabled={true}></CheckBox>
`
const Code3 = `const [check, setCheck] = useState(false)
<div className="margin-right-small">
  <CheckBox checked={check}></CheckBox>
</div>`
const Code4 = `const group = [
    {
      value: '',
      label: ''
    },
    {
      value: '',
      label: ''
    },
    {
      value: '',
      label: ''
    },
    {
      value: '',
      label: ''
    },
  ]
  
 <CheckBoxGroupNext tittle={''} options={group} defaultValue={['']}
`

const CheckBoxDisplay = () => {
  const [check, setCheck] = useState(false)
  const handleClick = () => {
    setCheck(!check)
  }

  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>Checkbox </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}>CheckBox</p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={'/'}
              code={Code1}
          >
            <CheckBox defaultChecked={true}></CheckBox>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code2}
          >
            <span className={'margin-right-small'}>
              <CheckBox checked={true}></CheckBox>
            </span>
            <span className={'margin-right-small'}>
              <CheckBox indeterminate={true}></CheckBox>
            </span>
            <span className={'margin-right-small'}>
              <CheckBox checked={true} disabled={true}></CheckBox>
            </span>
            <span className={'margin-right-small'}>
              <CheckBox disabled={true}></CheckBox>
            </span>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code3}
          >
            <div className="margin-right-small">
              <CheckBox checked={check}></CheckBox>
            </div>
            <Button size={'tiny'} onClick={handleClick}></Button>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code4}
          >
            <CheckBoxGroupNext tittle={''} options={group} defaultValue={['']}/>
          </Card>
        </div>
        <h3 className={'display-tittle'}>API</h3>
        <h3 className={'display-tittle'}>CheckBox</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource}/>
        </div>
        <h3 className={'display-tittle'}>CheckBoxGroup</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource2}/>
        </div>
        <h3 className={'display-tittle'}>Option</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={optionDataSpurce}/>
        </div>

      </div>
  )
}

export default CheckBoxDisplay
