import React, {useState} from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import Progress from "../../components/Progress/progress";

import Button from "../../components/Button/button";
const columns = [
  {
    title: '',
    dataIndex: 'Attribute',
    key: 'Attribute',

  },
  {
    title: '',
    dataIndex: 'Description',
    key: 'Description'
  },
  {
    title: '',
    dataIndex: 'Type',
    key: 'Type'
  },{
    title: '',
    dataIndex: 'Default',
    key: 'Default'
  }
];
const dataSource =  [
  {
    key: '1',
    attribute: 'percent',
    description: '',
    type: `number`,
    default: `0`,
  },
  {
    key: '2',
    attribute: 'type',
    description: '',
    type: `'line' | 'circle'`,
    default: `'line'`,
  },
  {
    key: '3',
    attribute: 'showText',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '4',
    attribute: 'showAnimation',
    description: '100%,',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '5',
    attribute: 'color',
    description: '',
    type: 'string',
    default: '#1cb0f6()',
  }
]
const Code1 = `<Progress percent={rate}/>
`
const Code2 = `<Progress showText={true} percent={rate}/>
`
const Code3 = `<Progress color={'#ff9600'} showText={true} percent={rate}/>

<Progress color={'#ff4b4b'} showText={true} percent={rate}/>

<Progress color={'#89e219'} showText={true} percent={rate}/>

<Progress color={'#9069cd'} showText={true} percent={rate}/>
`
const Code4 = `<Progress color={'#ff9600'} showText={true} showAnimation={true} percent={rate}/>

`
const Code5 = `<Progress color={'#9069cd'} showText={true} type={"circle"} percent={rate}/>
`
const ProgressDisplay = () => {


  const [rate, setRate] = useState(30)
  const handleReduce = () => {
    setRate(rate - 10)
  }
  const handleAdd = () => {
    setRate(rate + 10)
  }
  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>Progress </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code1}
          >
            <div className="flex-column-wrapper">
              <div className="padding-bottom-small">
                <Progress percent={rate}/>
              </div>
              <div>
                <Button style={{marginRight: '5px'}} size={"tiny"} onClick={handleAdd}></Button>
                <Button size={"tiny"} onClick={handleReduce}></Button>
              </div>
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code2}
          >
            <div className="flex-column-wrapper">
              <div className="padding-bottom-small">
                <Progress showText={true} percent={rate}/>
              </div>
              <div>
                <Button style={{marginRight: '5px'}} size={"tiny"} onClick={handleAdd}></Button>
                <Button size={"tiny"} onClick={handleReduce}></Button>
              </div>
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code3}
          >
            <div className="flex-column-wrapper">
              <div className="padding-bottom-small">
                <Progress color={'#ff9600'} showText={true} percent={rate}/>
              </div>
              <div className="padding-bottom-small">
                <Progress color={'#ff4b4b'} showText={true} percent={rate}/>
              </div>
              <div className="padding-bottom-small">
                <Progress color={'#89e219'} showText={true} percent={rate}/>
              </div>
              <div className="padding-bottom-small">
                <Progress color={'#9069cd'} showText={true} percent={rate}/>
              </div>
              <div>
                <Button style={{marginRight: '5px'}} size={"tiny"} onClick={handleAdd}></Button>
                <Button size={"tiny"} onClick={handleReduce}></Button>
              </div>
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code4}
          >
            <div className="flex-column-wrapper">
              <div className="padding-bottom-small">
                <Progress color={'#ff9600'} showText={true} showAnimation={true} percent={rate}/>
              </div>
              <div>
                <Button style={{marginRight: '5px'}} size={"tiny"} onClick={handleAdd}></Button>
                <Button size={"tiny"} onClick={handleReduce}></Button>
              </div>
            </div>
          </Card>
        </div>

        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code5}
          >
            <div className="flex-column-wrapper">
              <div className="padding-bottom-small">
                <Progress color={'#9069cd'} showText={true} type={"circle"} percent={rate}/>
              </div>
              <div>
                <Button style={{marginRight: '5px'}} size={"tiny"} onClick={handleAdd}></Button>
                <Button size={"tiny"} onClick={handleReduce}></Button>
              </div>
            </div>
          </Card>
        </div>


        <h3 className={'display-tittle'}>API</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource}/>
        </div>

      </div>
  )
}

export default ProgressDisplay
