import React from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import AutoComplete from "../../components/AutoComplete/autoComplete";

const AutoCompleteDisplay = () => {
  const columns = [
    {
      title: '',
      dataIndex: 'Attribute',
      key: 'Attribute',

    },
    {
      title: '',
      dataIndex: 'Description',
      key: 'Description'
    },
    {
      title: '',
      dataIndex: 'Type',
      key: 'Type'
    },{
      title: '',
      dataIndex: 'Default',
      key: 'Default'
    }
  ];
  const dataSource2 =  [
    {
      key: '1',
      attribute: 'value',
      description: '',
      type: `string`,
      default: ``,
    }
  ]
  const dataSource =  [
    {
      key: '1',
      attribute: 'fetchSuggestions',
      description: '',
      type: `(str: string) => DataSourceType[] | Promise`,
      default: ``,
    },
    {
      key: '2',
      attribute: 'onSelect',
      description: '',
      type: `(item: DataSourceType) => void`,
      default: ``,
    },
    {
      key: '3',
      attribute: 'renderOptions',
      description: '',
      type: '(item: DataSourceType) => ReactElement',
      default: '',
    }
  ]
  const Code1 = `const handleFetch = (query: string) => {
  return poems.filter(name => name.includes(query)).map(name => ({value: name}))
}

<AutoComplete
  defaultValue={''} 
  placeholder={''} 
  fetchSuggestions={handleFetch}
/>
`
  const Code2 = `const handleFetchData = (query: string) => {
  return fetch('https://api.github.com/search/users?q='+ query)
      .then(res => res.json())
      .then(({ items }) => {
        return items.slice(0, 10).map((item: any) => ({ value: item.login, ...item}))
      })
}

<AutoComplete placeholder={''} fetchSuggestions={handleFetchData}/>
`
  const lakers = ['', '', '', '', '',
    '', '', '', '', '', '', '']
  const handleFetchData = (query: string) => {
    return fetch('https://api.github.com/search/users?q='+ query)
        .then(res => res.json())
        .then(({ items }) => {
          return items.slice(0, 10).map((item: any) => ({ value: item.login, ...item}))
        })
  }
  const handleFetch = (query: string) => {
    return lakers.filter(name => name.includes(query)).map(name => ({value: name}))
  }
  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>AutoComplete </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code1}
          >
            <div className="flex-column-wrapper">
            <AutoComplete
                defaultValue={''}
                placeholder={''}
                fetchSuggestions={handleFetch}
            />
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code2}
          >
            <div className="flex-column-wrapper">
              <AutoComplete placeholder={''} fetchSuggestions={handleFetchData}/>
            </div>
          </Card>
        </div>

        <h3 className={'display-tittle'}>API</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource}/>
        </div>

        <h3 className={'display-tittle'}>DataSourceObject</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource2}/>
        </div>

      </div>
  )
}

export default AutoCompleteDisplay
