import React from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import Switch from "../../components/Switch/switch";
const columns = [
  {
    title: '',
    dataIndex: 'Attribute',
    key: 'Attribute',

  },
  {
    title: '',
    dataIndex: 'Description',
    key: 'Description'
  },
  {
    title: '',
    dataIndex: 'Type',
    key: 'Type'
  },{
    title: '',
    dataIndex: 'Default',
    key: 'Default'
  }
];
const dataSource =  [
  {
    key: '1',
    attribute: 'defaultValue',
    description: '',
    type: `boolean`,
    default: `false`,
  },
  {
    key: '2',
    attribute: 'value',
    description: '',
    type: `boolean`,
    default: `false`,
  },
  {
    key: '3',
    attribute: 'disabled',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '4',
    attribute: 'thin',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '5',
    attribute: 'onChange',
    description: '',
    type: '(value: boolean) => void',
    default: '',
  },
  {
    key: '6',
    attribute: 'activeText',
    description: '',
    type: 'string',
    default: '',
  },
  {
    key: '7',
    attribute: 'inactiveText',
    description: '',
    type: 'string',
    default: '',
  },
  {
    key: '8',
    attribute: 'switchStyle',
    description: '',
    type: `'slides' | 'button'`,
    default: `'slides'`,
  }
]
const Code1 = `<Switch 
  defaultValue={true} 
  activeText={'opened'} 
  inactiveText={'closed'}
/>
`
const Code2 = `<Switch 
  defaultValue={true} 
  activeText={'opened'} 
  inactiveText={'closed'}
/>
`
const Code3 = `<Switch disabled={true}/>
`
const Code4 = `<Switch 
  defaultValue={true} 
  thin={true}
/>

<Switch defaultValue={true}/>
`
const Code5 = `<Switch 
  inactiveText={''} 
  activeText={''} 
  defaultValue={true} 
  switchStyle={"button"}
/>
`
const SwitchDisplay = () => {

  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>Switch </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code1}
          >
            <Switch/>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code2}
          >
            <Switch defaultValue={true} activeText={'opened'} inactiveText={'closed'}/>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code3}
          >
            <div className="margin-right-small">
              <Switch disabled={true}/>
            </div>
            <Switch disabled={true} defaultValue={true}/>
          </Card>
        </div>

        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code4}
          >
            <div className="margin-right-small">
              <Switch defaultValue={true} thin={true}/>
            </div>
            <Switch defaultValue={true}/>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code5}
          >
            <Switch inactiveText={''} activeText={''} defaultValue={true} switchStyle={"button"}/>
          </Card>
        </div>

        <h3 className={'display-tittle'}>API</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource}/>
        </div>

      </div>
  )
}

export default SwitchDisplay
