import React, {useState} from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import Popover from "../../components/Popover/popover";
import Button from "../../components/Button/button";
import {ReactComponent as DemondSvg} from '../svg/demond.svg'
import {ReactComponent as DemondItemSvg} from '../svg/demond-item.svg'
import {ReactComponent as CompleteSvg} from '../svg/complete.svg'
import {ReactComponent as WraningSvg} from '../svg/wraning.svg'



const columns = [
  {
    title: '',
    dataIndex: 'Attribute',
    key: 'Attribute',

  },
  {
    title: '',
    dataIndex: 'Description',
    key: 'Description'
  },
  {
    title: '',
    dataIndex: 'Type',
    key: 'Type'
  },{
    title: '',
    dataIndex: 'Default',
    key: 'Default'
  }
];
const dataSource =  [
  {
    key: '1',
    attribute: 'visible',
    description: '',
    type: `boolean`,
    default: ``,
  },
  {
    key: '2',
    attribute: 'content',
    description: '',
    type: `string | React.ReactNode`,
    default: ``,
  },
  {
    key: '3',
    attribute: 'position',
    description: '',
    type: `'top' | 'left' | 'right' | 'bottom'`,
    default: `'top'`,
  },
  {
    key: '4',
    attribute: 'color',
    description: '',
    type: 'string',
    default: '',
  },
  {
    key: '5',
    attribute: 'showAnimation',
    description: '',
    type: 'boolean',
    default: 'false',
  }
]
const Code1 = `<Popover content={''}>
    <span className={'pop-anchor'}></span>
  </Popover>
  <Popover content={''}>
    <span className={'pop-anchor'}></span>
  </Popover><Popover content={''}>
  <span className={'pop-anchor'}></span>
</Popover><Popover content={''}>
  <span className={'pop-anchor'}></span>
</Popover>
`
const Code2 = `<div className="margin-right-large">
  <Popover content={'Top!'}>
    <span className={'pop-anchor'}></span>
  </Popover>
</div>
<div className="margin-right-large">
  <Popover zIndex={50} position={'right'} content={'Right!'}>
    <span className={'pop-anchor'}></span>
  </Popover>
</div>
<div className="margin-right-large">
  <Popover position={'bottom'} content={'Bottom!'}>
    <span className={'pop-anchor'}></span>
  </Popover>
</div>
<Popover position={'left'} content={'Left!'}>
  <span className={'pop-anchor'}></span>
</Popover>
`
const Code3 = `<Popover bgColor={'#ffffff'} content={cardElement}>
  <DemondItemSvg/>
</Popover>
`
const Code4 = `<Popover
    content={''}
    visible={visible}
    dark={true}
>
  <Button
      onClick={handleClick}
      size={'tiny'}
  >
    
  </Button>
</Popover>
`

const PopoverDisplay = () => {
  const [visible, setVisible] = useState(false)
  const handleClick = () => {
    setVisible(!visible)
  }
  const cardElement = (
      <div className="popover-inner-card">
        <div className="popover-inner-card__image">
          <DemondSvg/>
        </div>
        <div className="popover-inner-card__content">
          <span className={'tittle'}></span>
          <span className={'info'}>5</span>
          <Button size={'tiny'} btnType={'none'}></Button>
        </div>
      </div>
  )

  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>Popover </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code1}
          >
            <Popover content={''}>
              <span className={'pop-anchor'}></span>
            </Popover>
            <Popover content={''}>
              <span className={'pop-anchor'}></span>
            </Popover><Popover content={''}>
            <span className={'pop-anchor'}></span>
          </Popover><Popover content={''}>
            <span className={'pop-anchor'}></span>
          </Popover>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code2}
          >
            <div className="margin-right-large">
              <Popover content={'Top!'}>
                <span className={'pop-anchor'}></span>
              </Popover>
            </div>
            <div className="margin-right-large">
              <Popover zIndex={50} position={'right'} content={'Right!'}>
                <span className={'pop-anchor'}></span>
              </Popover>
            </div>
            <div className="margin-right-large">
              <Popover position={'bottom'} content={'Bottom!'}>
                <span className={'pop-anchor'}></span>
              </Popover>
            </div>
            <Popover position={'left'} content={'Left!'}>
              <span className={'pop-anchor'}></span>
            </Popover>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code3}
          >
            <Popover bgColor={'#ffffff'} content={cardElement}>
              <DemondItemSvg/>
            </Popover>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={'/'}
              explain={''}
              code={Code3}
          >
            <div className="margin-right-large">
              <Popover showAnimation={false} bgColor={'#58cc02'} content={''}>
                <CompleteSvg/>
              </Popover>
            </div>
            <Popover showAnimation={false} bgColor={'#ff9600'} content={''}>
              <WraningSvg/>
            </Popover>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code4}
          >
            <Popover
                content={''}
                visible={visible}
                dark={true}
            >
              <Button
                  onClick={handleClick}
                  size={'tiny'}
              >
                
              </Button>
            </Popover>
          </Card>
        </div>
        <h3 className={'display-tittle'}>API</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource}/>
        </div>

      </div>
  )
}

export default PopoverDisplay
