import React from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import Pagination from "../../components/Pager/pagination";
const columns = [
  {
    title: '',
    dataIndex: 'Attribute',
    key: 'Attribute',

  },
  {
    title: '',
    dataIndex: 'Description',
    key: 'Description'
  },
  {
    title: '',
    dataIndex: 'Type',
    key: 'Type'
  },{
    title: '',
    dataIndex: 'Default',
    key: 'Default'
  }
];
const dataSource =  [
  {
    key: '1',
    attribute: 'defaultCurrent',
    description: '',
    type: `number`,
    default: `1`,
  },
  {
    key: '2',
    attribute: 'total',
    description: '',
    type: `number`,
    default: ``,
  },
  {
    key: '3',
    attribute: 'onChange',
    description: '',
    type: '(index: number) => void',
    default: '',
  },
  {
    key: '4',
    attribute: 'showSizeChanger',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '5',
    attribute: 'showQuickJumper',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '6',
    attribute: 'simple',
    description: '',
    type: 'boolean',
    default: 'false',
  }
]
const Code1 = ` <Pagination total={60}/>
`
const Code2 = `<Pagination total={200} defaultCurrent={12}/>
`
const Code3 = `<Pagination total={500} showSizeChanger={true}/>
`
const Code4 = `<Pagination total={500} showQuickJumper={true}/>
`
const Code5 = `<Pagination total={200} defaultCurrent={9} simple={true}/>
`


const PagerDisplay = () => {
  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>Pagination </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code1}
          >
            <Pagination total={60}/>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code2}
          >
            <Pagination total={200} defaultCurrent={12}/>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code3}
          >
            <Pagination total={500} showSizeChanger={true}/>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code4}
          >
            <Pagination total={500} showQuickJumper={true}/>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code5}
          >
            <Pagination total={200} defaultCurrent={9} simple={true}/>
          </Card>
        </div>
        <h3 className={'display-tittle'}>API</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource}/>
        </div>

      </div>
  )
}

export default PagerDisplay
