import React, {useState} from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import Message from "../../components/Message/message";
import Modal from '../../components/Modal/modal'
import Button from "../../components/Button/button";
import {ReactComponent as DemondSvg} from "../svg/demond.svg";

const columns = [
  {
    title: '',
    dataIndex: 'Attribute',
    key: 'Attribute',

  },
  {
    title: '',
    dataIndex: 'Description',
    key: 'Description'
  },
  {
    title: '',
    dataIndex: 'Type',
    key: 'Type'
  },{
    title: '',
    dataIndex: 'Default',
    key: 'Default'
  }
];
const dataSource =  [
  {
    key: '1',
    attribute: 'type',
    description: '',
    type: `'success' | 'primary' | 'error'`,
    default: `'success'`,
  },
  {
    key: '2',
    attribute: 'showMessage',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '3',
    attribute: 'showButton',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '4',
    attribute: 'tittle',
    description: '',
    type: 'string',
    default: '',
  },
  {
    key: '5',
    attribute: 'info',
    description: '',
    type: 'string',
    default: '',
  },
  {
    key: '6',
    attribute: 'btnText',
    description: '',
    type: 'string',
    default: '',
  },
  {
    key: '7',
    attribute: 'btnSize',
    description: '',
    type: `'tiny' | 'middle' | 'full'`,
    default: `'middle'`,
  },
  {
    key: '8',
    attribute: 'style',
    description: ' Button ',
    type: 'React.CSSProperties',
    default: '',
  },
  {
    key: '9',
    attribute: 'onClickButton',
    description: '',
    type: '() => void',
    default: '',
  },
  {
    key: '10',
    attribute: 'onClose',
    description: '',
    type: '() => void',
    default: '',
  },
  {
    key: '11',
    attribute: 'duration',
    description: '',
    type: 'number',
    default: '',
  }
]
const MessageDisplay = () => {

  const Code1 = `<Message 
  showMessage={appear} 
  tittle={'Tittle'} 
  info={'content content'}
/>
<Message 
  type={"error"} 
  showMessage={appear2} 
  tittle={'Tittle'} 
  info={'content content'}
/>
<Message 
  type={"primary"} 
  showMessage={appear3} 
  tittle={'Tittle'} 
  info={'content content'}
/>
`
  const Code2 = `<Message 
  onClickButton={handleClick4} 
  btnText={''} 
  showButton={true} 
  showMessage={appear4} 
  tittle={''} 
  info={''}
/>

<Message 
  onClickButton={handleClick5} 
  btnText={''} 
  showButton={true} 
  type={"error"} 
  showMessage={appear5} 
  tittle={''} 
  info={''}
/>

<Message 
  onClickButton={handleClick6} 
  btnText={''} 
  showButton={true} 
  type={"primary"} 
  showMessage={appear6} 
  tittle={''} 
  info={''}
/>
`
  const Code3 = `<Modal
  tittle={'Tittle'}
  section={'content content content content'}
  detail={'content content content content'}
  showAlert={appear7}
  alertController={handleClick7}
    >
    <div className="padding-bottom-small">
      <Button onClick={handleClick7} btnType={'primary'} size={'full'}></Button>
    </div>
    <Button onClick={handleClick7} size={'full'}></Button>
</Modal>
`
  const Code4 = `<Modal
  showAlert={appear8}
  alertController={handleClick8}
  >
    {cardElement}
</Modal>
`
  const [appear, setAppear] = useState(false)

  const handleClick = () => {
    setAppear(!appear)
  }

  const [appear2, setAppear2] = useState(false)
  const handleClick2 = () => {
    setAppear2(!appear2)
  }

  const [appear3, setAppear3] = useState(false)
  const handleClick3 = () => {
    setAppear3(!appear3)
  }
  const [appear4, setAppear4] = useState(false)
  const handleClick4 = () => {
    setAppear4(!appear4)
  }
  const [appear5, setAppear5] = useState(false)
  const handleClick5 = () => {
    setAppear5(!appear5)
  }
  const [appear6, setAppear6] = useState(false)
  const handleClick6 = () => {
    setAppear6(!appear6)
  }
  const [appear7, setAppear7] = useState(false)
  const handleClick7 = () => {
    setAppear7(!appear7)
  }
  const [appear8, setAppear8] = useState(false)
  const handleClick8 = () => {
    setAppear8(!appear8)
  }
  const cardElement = (
      <div className="popover-inner-card">
        <div className="popover-inner-card__image">
          <DemondSvg/>
        </div>
        <div className="popover-inner-card__content">
          <span className={'tittle'}></span>
          <span className={'info'}>5</span>
          <Button size={'tiny'} btnType={'none'}></Button>
        </div>
      </div>
  )
  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>Dialog </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={'Banner'}
              explain={''}
              code={Code1}
          >

            <Message showMessage={appear} tittle={'Tittle'} info={'content content'}/>
            <Message type={"error"} showMessage={appear2} tittle={'Tittle'} info={'content content'}/>
            <Message type={"primary"} showMessage={appear3} tittle={'Tittle'} info={'content content'}/>
            <Button btnType={'success'} style={{marginRight: '10px'}} size={'tiny'} onClick={handleClick}>success</Button>
            <Button btnType={'error'} style={{marginRight: '10px'}} size={'tiny'} onClick={handleClick2}>error</Button>
            <Button btnType={'primary'} size={'tiny'} onClick={handleClick3}>primary</Button>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={'Banner/'}
              explain={''}
              code={Code2}
          >
            <Message onClickButton={handleClick4} btnText={''} showButton={true} showMessage={appear4} tittle={''} info={''}/>
            <Message onClickButton={handleClick5} btnText={''} showButton={true} type={"error"} showMessage={appear5} tittle={''} info={''}/>
            <Message onClickButton={handleClick6} btnText={''} showButton={true} type={"primary"} showMessage={appear6} tittle={''} info={''}/>
            <Button btnType={'success'} style={{marginRight: '10px'}} size={'tiny'} onClick={handleClick4}>success</Button>
            <Button btnType={'error'} style={{marginRight: '10px'}} size={'tiny'} onClick={handleClick5}>error</Button>
            <Button btnType={'primary'} size={'tiny'} onClick={handleClick6}>primary</Button>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={'Model'}
              explain={''}
              code={Code3}
          >
            <Modal
                tittle={'Tittle'}
                section={'content content content content'}
                detail={'content content content content'}
                showAlert={appear7}
                alertController={handleClick7}>
              <div className="padding-bottom-small">
                <Button onClick={handleClick7} btnType={'primary'} size={'full'}></Button>
              </div>
              <Button onClick={handleClick7} size={'full'}></Button>
            </Modal>
            <Button size={'tiny'} onClick={handleClick7}>Model</Button>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={'Model/'}
              explain={''}
              code={Code4}
          >
            <Modal
                showAlert={appear8}
                alertController={handleClick8}>
              {cardElement}
            </Modal>
            <Button size={'tiny'} onClick={handleClick8}>Model</Button>
          </Card>
        </div>
        <h3 className={'display-tittle'}>API</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource}/>
        </div>

      </div>
  )
}

export default MessageDisplay
