import React, {useState} from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import Popconfirm from '../../components/Popconfirm/popconfirm'
import Button from "../../components/Button/button";
import useDebounce from "../../hooks/useDebounce";

const columns = [
  {
    title: '',
    dataIndex: 'Attribute',
    key: 'Attribute',

  },
  {
    title: '',
    dataIndex: 'Description',
    key: 'Description'
  },
  {
    title: '',
    dataIndex: 'Type',
    key: 'Type'
  },{
    title: '',
    dataIndex: 'Default',
    key: 'Default'
  }
];
const dataSource =  [
  {
    key: '1',
    attribute: 'visible',
    description: '',
    type: `boolean`,
    default: `false`,
  },
  {
    key: '2',
    attribute: 'tittle',
    description: '',
    type: `string`,
    default: ``,
  },
  {
    key: '3',
    attribute: 'detail',
    description: '',
    type: 'string',
    default: '',
  },
  {
    key: '5',
    attribute: 'position',
    description: `'top' | 'left' | 'right' | 'bottom'`,
    type: 'string',
    default: '',
  },
  {
    key: '6',
    attribute: 'bgColor',
    description: '',
    type: 'string',
    default: '#ffc800()',
  },
  {
    key: '7',
    attribute: 'textColor',
    description: '',
    type: 'string',
    default: '#ffffff()',
  },
  {
    key: '8',
    attribute: 'firstButtonText',
    description: '',
    type: 'string',
    default: '',
  },
  {
    key: '9',
    attribute: 'secondButtonText',
    description: '',
    type: 'string',
    default: '',
  },
  {
    key: '10',
    attribute: 'onFirstButtonClick',
    description: '',
    type: '() => void',
    default: '',
  },
  {
    key: '11',
    attribute: 'onSecondButtonClick',
    description: '',
    type: '() => void',
    default: '',
  },
  {
    key: '12',
    attribute: 'replaceFirstButton',
    description: '',
    type: 'React.ReactNode',
    default: '',
  },
  {
    key: '13',
    attribute: 'replaceSecondButton',
    description: '',
    type: 'React.ReactNode',
    default: '',
  },
  {
    key: '14',
    attribute: 'style',
    description: 'Popconfirm',
    type: 'CSSProperties',
    default: '',
  }
]
const Code1 = `<Popconfirm
  tittle={''}
  detail={'4/21'}
  visible={visible}
  secondButtonText={''}
  firstButtonText={''}
  onFirstButtonClick={handleClick}
>
    <Button onClick={handleClick} btnType={'none'}></Button>
</Popconfirm>
`
const Code2 = `<Popconfirm
    bgColor={'#ff4b4b'}
    tittle={''}
    detail={'4/21'}
    visible={visible3}
    secondButtonText={''}
    firstButtonText={''}
    onFirstButtonClick={handleClick3}
>
  <Button
      onClick={handleClick3}
      btnType={'none'}></Button>
</Popconfirm>

<Popconfirm
    bgColor={'#58cc02'}
    tittle={''}
    detail={'4/21'}
    position={'bottom'}
    visible={visible4}
    secondButtonText={''}
    firstButtonText={''}
    onFirstButtonClick={handleClick4}
>
  <Button
      onClick={handleClick4}
      btnType={'none'}></Button>
</Popconfirm>
`
const Code3 = `<Popconfirm
  bgColor={'#f7f7f7'}
  textColor={'#777777'}
  tittle={''}
  detail={'4/21'}
  visible={visible6}
  replaceFirstButton={greenButton}
  replaceSecondButton={garyButton}
>
<Button
    onClick={handleClick6}
    btnType={'none'}></Button>
</Popconfirm>
`
const Code4 = `<Popconfirm
    bgColor={'#f7f7f7'}
    textColor={'#777777'}
    tittle={'?'}
    detail={''}
    visible={visible5}
    replaceFirstButton={greenButton2}
    replaceSecondButton={garyButton2}
>
  <Button
      onClick={handleClick5}
      btnType={'none'}
  >Delete</Button>
</Popconfirm>
`
const Code5 = `
`
const PopconfirmDisplay = () => {
  const [visible, setVisible] = useState(false)
  const handleClick = () => {
    setVisible(!visible)
  }
  const [visible2, setVisible2] = useState(false)
  const handleClick2 = () => {
    setVisible2(!visible2)
  }
  const [visible6, setVisible6] = useState(false)
  const handleClick6 = () => {
    setVisible6(!visible6)
  }
  const [visible3, setVisible3] = useState(false)
  const [visible4, setVisible4] = useState(false)
  const [visible5, setVisible5] = useState(false)
  const [loading, setLoading] = useState(false)
  const handleClick3 = () => {
    setVisible3(!visible3)
  }
  const handleClick4 = () => {
    setVisible4(!visible4)
  }
  const handleClick5 = () => {
    setVisible5(!visible5)
  }



  const handleChecked = () => {
    clearTimeout(timer)
    setLoading(true)
    timer = setTimeout(() => {

      setVisible5(!visible5)
      setLoading(false)
    }, 2000)
  }
  const handleCancel = () => {
    setVisible5(!visible5)
  }
  const debounceLoading = useDebounce(loading, 200)
  const greenButton = <Button onClick={handleClick6} btnType={'success'} size={'full'}></Button>
  const garyButton = <Button size={'full'}></Button>
  const greenButton2 = <Button onClick={handleChecked} isLoading={debounceLoading} btnType={'primary'} size={'full'}></Button>
  const garyButton2 = <Button onClick={handleCancel} size={'full'}></Button>
  let timer: NodeJS.Timeout

  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>Popconfirm </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code1}
          >
            <Popconfirm
                tittle={''}
                detail={'4/21'}
                visible={visible}
                secondButtonText={''}
                firstButtonText={''}
                onFirstButtonClick={handleClick}
            >
              <Button onClick={handleClick} btnType={'none'}></Button>
            </Popconfirm>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={'/'}
              explain={''}
              code={Code2}
          >
            <div className="margin-right-small">
              <Popconfirm
                  bgColor={'#ff4b4b'}
                  tittle={''}
                  detail={'4/21'}
                  visible={visible3}
                  secondButtonText={''}
                  firstButtonText={''}
                  onFirstButtonClick={handleClick3}
              >
                <Button
                    onClick={handleClick3}
                    btnType={'none'}></Button>
              </Popconfirm>
            </div>
            <Popconfirm
                bgColor={'#58cc02'}
                tittle={''}
                detail={'4/21'}
                position={'bottom'}
                visible={visible4}
                secondButtonText={''}
                firstButtonText={''}
                onFirstButtonClick={handleClick4}
            >
              <Button
                  onClick={handleClick4}
                  btnType={'none'}></Button>
            </Popconfirm>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={','}
              code={Code3}
          >
            <Popconfirm
                bgColor={'#f7f7f7'}
                textColor={'#777777'}
                tittle={''}
                detail={'4/21'}
                visible={visible6}
                replaceFirstButton={greenButton}
                replaceSecondButton={garyButton}
            >
              <Button
                  onClick={handleClick6}
                  btnType={'none'}></Button>
            </Popconfirm>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code4}
          >
            <Popconfirm
                bgColor={'#f7f7f7'}
                textColor={'#777777'}
                tittle={'?'}
                detail={''}
                visible={visible5}
                replaceFirstButton={greenButton2}
                replaceSecondButton={garyButton2}
            >
              <Button
                  onClick={handleClick5}
                  btnType={'none'}
              >Delete</Button>
            </Popconfirm>
          </Card>
        </div>


        <h3 className={'display-tittle'}>API</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource}/>
        </div>

      </div>
  )
}

export default PopconfirmDisplay
