import React from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import Carousel from "../../components/Carousel/carousel";
const columns = [
  {
    title: '',
    dataIndex: 'Attribute',
    key: 'Attribute',

  },
  {
    title: '',
    dataIndex: 'Description',
    key: 'Description'
  },
  {
    title: '',
    dataIndex: 'Type',
    key: 'Type'
  },{
    title: '',
    dataIndex: 'Default',
    key: 'Default'
  }
];
const dataSource =  [
  {
    key: '1',
    attribute: 'autoplay',
    description: '',
    type: `boolean`,
    default: `false`,
  },
  {
    key: '2',
    attribute: 'interval',
    description: '(/ms)',
    type: `number`,
    default: `3000`,
  },
  {
    key: '3',
    attribute: 'defaultIndex',
    description: '',
    type: 'number',
    default: '1',
  },
  {
    key: '4',
    attribute: 'dot',
    description: '',
    type: `boolean`,
    default: `true`,
  },
  {
    key: '5',
    attribute: 'control',
    description: '',
    type: 'boolean',
    default: 'true',
  }
]
const CarouseCard = ({children}: {children: React.ReactNode}) => {
  return (
      <div style={{
        width: '550px',
        height: '300px',
        lineHeight: '300px',
        textAlign: 'center',
        backgroundColor: '#2b70c9',
        color: '#ffffff',
        fontSize: '2rem'
      }}>{children}</div>
  )
}
const Code1 = `const CarouseCard = ({children}: {children: React.ReactNode}) => {
  return (
      <div style={{
        width: '550px',
        height: '300px',
        lineHeight: '300px',
        textAlign: 'center',
        backgroundColor: '#2b70c9',
        color: '#ffffff',
        fontSize: '2rem'
      }}>{children}</div>
  )
}

<Carousel>
  <CarouseCard>1</CarouseCard>
  <CarouseCard>2</CarouseCard>
  <CarouseCard>3</CarouseCard>
  <CarouseCard>4</CarouseCard>
  <CarouseCard>5</CarouseCard>
  <CarouseCard>6</CarouseCard>
</Carousel>
`
const Code2 = `const CarouseCard = ({children}: {children: React.ReactNode}) => {
  return (
      <div style={{
        width: '550px',
        height: '300px',
        lineHeight: '300px',
        textAlign: 'center',
        backgroundColor: '#2b70c9',
        color: '#ffffff',
        fontSize: '2rem'
      }}>{children}</div>
  )
}

<Carousel autoplay={true} interval={3000}>
  <CarouseCard>1</CarouseCard>
  <CarouseCard>2</CarouseCard>
  <CarouseCard>3</CarouseCard>
  <CarouseCard>4</CarouseCard>
  <CarouseCard>5</CarouseCard>
  <CarouseCard>6</CarouseCard>
</Carousel>
`

const CarouselDisplay = () => {


  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>Carousel </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={','}
              code={Code1}
          >
            <Carousel>
              <CarouseCard>1</CarouseCard>
              <CarouseCard>2</CarouseCard>
              <CarouseCard>3</CarouseCard>
              <CarouseCard>4</CarouseCard>
              <CarouseCard>5</CarouseCard>
              <CarouseCard>6</CarouseCard>
            </Carousel>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={'.'}
              code={Code2}
          >
            <Carousel autoplay={true} interval={3000}>
              <CarouseCard>1</CarouseCard>
              <CarouseCard>2</CarouseCard>
              <CarouseCard>3</CarouseCard>
              <CarouseCard>4</CarouseCard>
              <CarouseCard>5</CarouseCard>
              <CarouseCard>6</CarouseCard>
            </Carousel>
          </Card>
        </div>

        <h3 className={'display-tittle'}>API</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource}/>
        </div>

      </div>
  )
}

export default CarouselDisplay
