import React, {useState} from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import Rate from "../../components/Rate/rate";
import Button from '../../components/Button/button'
const columns = [
  {
    title: '',
    dataIndex: 'Attribute',
    key: 'Attribute',

  },
  {
    title: '',
    dataIndex: 'Description',
    key: 'Description'
  },
  {
    title: '',
    dataIndex: 'Type',
    key: 'Type'
  },{
    title: '',
    dataIndex: 'Default',
    key: 'Default'
  }
];
const dataSource =  [
  {
    key: '1',
    attribute: 'defaultValue',
    description: '',
    type: `number`,
    default: `0`,
  },
  {
    key: '2',
    attribute: 'value',
    description: '',
    type: `number`,
    default: ``,
  },
  {
    key: '3',
    attribute: 'allowHalf',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '4',
    attribute: 'allowHalf',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '5',
    attribute: 'href',
    description: '',
    type: 'string',
    default: '',
  },
  {
    key: '6',
    attribute: 'count',
    description: '',
    type: 'number',
    default: '5',
  },
  {
    key: '7',
    attribute: 'onChange',
    description: '10.5',
    type: '(newValue: number) => void',
    default: '',
  },
  {
    key: '8',
    attribute: 'tip',
    description: ' ',
    type: 'string',
    default: '',
  }
]
const Code1 = `<Rate defaultValue={3.8}/>
`
const Code2 = `<Rate onChange={handleChange} tip={orderText} defaultValue={1.8}/>
`
const Code3 = `<Rate allowHalf={true} defaultValue={4.5}/>
`
const Code4 = `<Rate defaultValue={3.8} tip={''}/>

<Rate allowClear={false} defaultValue={3.8} tip={''}/>
`
const Code5 = `<Rate allowClear={true} defaultValue={2} count={10}/>
`
const Code6 = `<Rate disabled={true} defaultValue={3.8} count={5}/>
`
const Code7 = `<Rate value={orderRate} count={5}/>
`
const RateDisplay = () => {
  const text = ['terrible', 'bad', 'normal', 'good', 'wonderful']
  const [orderRate, setOrderRate] = useState(2)
  const [orderText, setOrderText] = useState(text[orderRate - 1])
  const handleSelectNumber = (index: number) => {
    setOrderRate(index)
  }
  const handleChange = (number: number) => {
    setOrderText(text[number - 1])
  }


  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>Rate </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}>.</p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code1}
          >
            <Rate defaultValue={3.8}/>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code2}
          >
            <Rate onChange={handleChange} tip={orderText} defaultValue={1.8}/>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code3}
          >
            <Rate allowHalf={true} defaultValue={4.5}/>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={'0'}
              code={Code4}
          >
            <div className="flex-column-wrapper">
                <Rate defaultValue={3.8} tip={''}/>
            </div>
            <Rate allowClear={false} defaultValue={3.8} tip={''}/>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code5}
          >
            <Rate allowClear={true} defaultValue={2} count={10}/>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code6}
          >
            <Rate disabled={true} defaultValue={3.8} count={5}/>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code7}
          >

              <Rate value={orderRate} count={5}/>

            <Button onClick={() => handleSelectNumber(3)} style={{marginRight: '10px',marginLeft: '10px'}} size={'tiny'}>3</Button>
            <Button onClick={() => handleSelectNumber(4)} style={{marginRight: '10px'}} size={'tiny'}>4</Button>
            <Button onClick={() => handleSelectNumber(5)} style={{marginRight: '10px'}} size={'tiny'}>5</Button>


          </Card>
        </div>
        <h3 className={'display-tittle'}>API</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource}/>
        </div>

      </div>
  )
}

export default RateDisplay
