import React, {useState} from 'react'
import Card from '../../components/Card/card'
import Button from "../../components/Button/button"
import Table from "../../components/Table/table";
const ButtonDisplay = () => {
  const [loading, setLoading] = useState(true)
  const handleClick = () => {
    setLoading(!loading)
  }
  const columns = [
    {
      title: '',
      dataIndex: 'Attribute',
      key: 'Attribute',

    },
    {
      title: '',
      dataIndex: 'Description',
      key: 'Description'
    },
    {
      title: '',
      dataIndex: 'Type',
      key: 'Type'
    },{
      title: '',
      dataIndex: 'Default',
      key: 'Default'
    }
  ];
  const dataSource =  [
    {
      key: '1',
      attribute: 'btnType',
      description: '',
      type: `'none' | 'white' | 'ghost' | 'success' | 'primary' | 'error'| 'link' | 'default'`,
      default: `'default'`,
    },
    {
      key: '2',
      attribute: 'size',
      description: '',
      type: `'tiny' | 'middle' | 'full'`,
      default: `'middle'`,
    },
    {
      key: '3',
      attribute: 'isLoading',
      description: '',
      type: 'boolean',
      default: 'false',
    },
    {
      key: '4',
      attribute: 'disabled',
      description: '',
      type: 'boolean',
      default: 'false',
    },
    {
      key: '5',
      attribute: 'href',
      description: '',
      type: 'string',
      default: '',
    },
    {
      key: '6',
      attribute: 'onClick',
      description: '',
      type: 'React.MouseEventHandler',
      default: '',
    },
    {
      key: '7',
      attribute: 'className',
      description: ' Button ',
      type: 'string',
      default: '',
    },
    {
      key: '8',
      attribute: 'style',
      description: ' Button ',
      type: 'React.CSSProperties',
      default: '',
    }
  ]
  const button1Code = `<Button btnType={'success'}>success</Button>
<Button btnType={'primary'}>normal</Button>
<Button btnType={'error'}>error</Button>
<Button>default</Button>
`
  const button2Code = `<Button btnType={'primary'} isLoading={true}>primary</Button>
<Button disabled={true}>default</Button>
`
  const button3Code = `<Button btnType={'link'} href={'/'}></Button>
<Button btnType={'none'}></Button>
`
  const button4Code = `<Button btnType={'white'}></Button>
<Button btnType={'ghost'}></Button>
`
  const button5Code = `<Button size={'tiny'}></Button>
<Button size={'middle'}></Button>
<Button size={'full'}></Button>`
  return (
    <div className={'display'}>
      <h2 className={'display-tittle'}>Button </h2>
      <p className={'display-explain'}></p>
      <h3 className={'display-tittle'}></h3>
      <p className={'display-explain'}></p>
      <h3 className={'display-tittle'}></h3>
      <div className="display-card-container">
        <Card
            divider={''}
            explain={''}
            code={button1Code}
        >
          <div style={{display: "flex",flexDirection: 'column'}}>
            <div  style={{marginBottom: '26px'}}>
              <Button style={{marginRight: '20px'}} btnType={'success'}>success</Button>
              <Button btnType={'primary'}>primary</Button>
            </div>
            <div>
              <Button  style={{marginRight: '20px'}}  btnType={'error'}>error</Button>
              <Button>default</Button>
            </div>
          </div>
        </Card>
      </div>
      <div className="display-card-container">
        <Card
            divider={''}
            explain={''}
            code={button2Code}
        >
          <Button style={{marginRight: '20px'}} isLoading={loading} btnType={'primary'}>success</Button>
          <Button style={{marginRight: '20px'}} disabled={true}>default</Button>
          <Button size={'tiny'} onClick={handleClick}></Button>
        </Card>
      </div>
      <div className="display-card-container">
        <Card
            divider={''}
            explain={''}
            code={button3Code}
        >
          <Button btnType={'link'} href={'/'}></Button>
          <Button btnType={'none'}></Button>
        </Card>
      </div>
      <div className="display-card-container">
        <Card
            divider={''}
            explain={''}
            code={button4Code}
            color={'#ff7878'}
        >
          <Button style={{marginRight: '20px'}} btnType={'white'}></Button>
          <Button btnType={'ghost'}></Button>
        </Card>
      </div>
      <div className="display-card-container">
        <Card
            divider={''}
            explain={':'}
            code={button5Code}
        >
          <Button
              style={{marginRight: '20px'}}
              size={'tiny'}>
            
          </Button>
          <Button
              style={{marginRight: '20px'}}
              size={'middle'}
          >
            
          </Button>
          <Button size={'full'}></Button>
        </Card>
      </div>
      <h3 className={'display-tittle'}>API</h3>
      <div className="display-table-container">
        <Table columns={columns} dataSource={dataSource}/>
      </div>

    </div>
  )
}

export default ButtonDisplay
