import React, {useState} from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import Tag from '../../components/Tag/tag'
import Input from "../../components/Input/input";
const columns = [
  {
    title: '',
    dataIndex: 'Attribute',
    key: 'Attribute',

  },
  {
    title: '',
    dataIndex: 'Description',
    key: 'Description'
  },
  {
    title: '',
    dataIndex: 'Type',
    key: 'Type'
  },{
    title: '',
    dataIndex: 'Default',
    key: 'Default'
  }
];
const dataSource =  [
  {
    key: '1',
    attribute: 'type',
    description: '',
    type: `'gray' | 'red' | 'yellow' | 'blue' | 'green' | 'purple'`,
    default: `'blue'`,
  },
  {
    key: '2',
    attribute: '',
    description: '',
    type: `boolean`,
    default: `false`,
  },
  {
    key: '3',
    attribute: 'closable',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '4',
    attribute: 'onTagClick',
    description: '',
    type: '() => void',
    default: '',
  },
  {
    key: '5',
    attribute: 'onClose',
    description: '',
    type: '() => void',
    default: '',
  },
  {
    key: '6',
    attribute: 'color',
    description: '',
    type: 'string',
    default: '',
  },
  {
    key: '7',
    attribute: 'size',
    description: '',
    type: `'normal' | 'small'`,
    default: `'normal'`,
  },
  {
    key: '8',
    attribute: 'checkable',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '9',
    attribute: 'checked',
    description: '',
    type: 'boolean',
    default: '',
  }
]
const Code1 = `<Tag type={'blue'}></Tag>
<Tag type={'red'}></Tag>
<Tag type={'yellow'}></Tag>
<Tag type={'green'}></Tag>
<Tag type={'purple'}></Tag>
<Tag type={'gray'}></Tag>
`
const Code2 = `<Tag type={'blue'} closable={true}></Tag>
<Tag type={'red'} closable={true}></Tag>
<Tag type={'yellow'} closable={true}></Tag>
<Tag type={'green'} closable={true}></Tag>
<Tag type={'purple'} closable={true}></Tag>
<Tag type={'gray'} closable={true}></Tag>
`
const Code3 = `<Tag type={'blue'} shallow={true} closable={true}></Tag>
<Tag type={'red'} shallow={true} closable={true}></Tag>
<Tag type={'yellow'} shallow={true} closable={true}></Tag>
<Tag type={'green'} shallow={true} closable={true}></Tag>
<Tag type={'purple'} shallow={true} closable={true}></Tag>
<Tag type={'gray'} shallow={true} closable={true}></Tag>
`
const Code4 = `<span style={{marginRight: '8px'}}>Favorite Foods:</span>
{orderTags.map((item,index) => {
  return <Tag 
      key={index} 
      closable={true} 
      onClose={() => handleClose(item)}
  >
    {item}
  </Tag>
})}
{!edit
    ? <Tag onTagClick={handleClickEdit} shallow={true}>+ </Tag>
    : <div style={{width: '135px'}}>
      <Input 
          onBlur={handleBlur} 
          autoFocus={true} 
          onPressEnter={handleEnter} 
          inputSize={"mini"}
      />
    </div>}
`
const Code5 = `<span style={{marginRight: '8px'}}>Categories:</span>
<Tag onTagClick={handleTagClick} checkable={true} checked={checked} type={'blue'} shallow={true} >Movies</Tag>
<Tag onTagClick={handleTagClick2} checkable={true} checked={checked2} type={'blue'} shallow={true} >Books</Tag>
<Tag onTagClick={handleTagClick3} checkable={true} checked={checked3} type={'blue'} shallow={true} >Musics</Tag>
<Tag onTagClick={handleTagClick4} checkable={true} checked={checked4} type={'blue'} shallow={true} >Sports</Tag>
`
const Code6 = `<Tag type={'blue'} closable={true}></Tag>
<Tag type={'red'} closable={true}></Tag>
<Tag type={'yellow'} closable={true}></Tag>

<Tag size={'small'} type={'blue'} closable={true}></Tag>
<Tag size={'small'} type={'red'} closable={true}></Tag>
<Tag size={'small'} type={'yellow'} closable={true}></Tag>
`
const TagDisplay = () => {


  const [edit, setEdit] = useState(false)
  const [orderTags, setOrderTags] = useState<string[]>([''])
  const handleClose = (value: string) => {
    const newValue = orderTags.filter((item) => item !== value)
    setOrderTags(newValue)
  }
  const handleClickEdit = () => {
    setEdit(true)
  }
  const handleEnter = (e: React.KeyboardEvent<HTMLInputElement>) => {
    const target = e.target as HTMLInputElement
    if (target.value) {
      setEdit(false)
      setOrderTags(orderTags.concat(target.value))
    } else {setEdit(false)
    }
  }
  const handleBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    const target = e.target as HTMLInputElement
    if (target.value) {
      setEdit(false)
      setOrderTags(orderTags.concat(target.value))
    } else {setEdit(false)
    }
  }
  const [checked, setChecked] = useState(true)
  const [checked2, setChecked2] = useState(false)
  const [checked3, setChecked3] = useState(false)
  const [checked4, setChecked4] = useState(false)
  const handleTagClick = () => {
    setChecked(!checked)
  }
  const handleTagClick2 = () => {
    setChecked2(!checked2)
  }
  const handleTagClick3 = () => {
    setChecked3(!checked3)
  }
  const handleTagClick4 = () => {
    setChecked4(!checked4)
  }
  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>Tag </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code1}
          >
            <Tag type={'blue'}></Tag>
            <Tag type={'red'}></Tag>
            <Tag type={'yellow'}></Tag>
            <Tag type={'green'}></Tag>
            <Tag type={'purple'}></Tag>
            <Tag type={'gray'}></Tag>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code2}
          >
            <Tag type={'blue'} closable={true}></Tag>
            <Tag type={'red'} closable={true}></Tag>
            <Tag type={'yellow'} closable={true}></Tag>
            <Tag type={'green'} closable={true}></Tag>
            <Tag type={'purple'} closable={true}></Tag>
            <Tag type={'gray'} closable={true}></Tag>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code3}
          >
            <Tag type={'blue'} shallow={true} closable={true}></Tag>
            <Tag type={'red'} shallow={true} closable={true}></Tag>
            <Tag type={'yellow'} shallow={true} closable={true}></Tag>
            <Tag type={'green'} shallow={true} closable={true}></Tag>
            <Tag type={'purple'} shallow={true} closable={true}></Tag>
            <Tag type={'gray'} shallow={true} closable={true}></Tag>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={' '}
              code={Code4}
          >
            <div className="flex-row-wrapper">
              <span style={{marginRight: '8px'}}>Favorite Foods:</span>
              {orderTags.map((item,index) => {
                return <Tag
                    key={index}
                    closable={true}
                    onClose={() => handleClose(item)}
                >
                  {item}
                </Tag>
              })}
              {!edit
                  ? <Tag onTagClick={handleClickEdit} shallow={true}>+ </Tag>
                  : <div style={{width: '135px'}}>
                    <Input
                        onBlur={handleBlur}
                        autoFocus={true}
                        onPressEnter={handleEnter}
                        inputSize={"mini"}
                    />
                  </div>}
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code5}
          >
            <div className="flex-row-wrapper">
              <span style={{marginRight: '8px'}}>Categories:</span>
              <Tag onTagClick={handleTagClick} checkable={true} checked={checked} type={'blue'} shallow={true} >Movies</Tag>
              <Tag onTagClick={handleTagClick2} checkable={true} checked={checked2} type={'blue'} shallow={true} >Books</Tag>
              <Tag onTagClick={handleTagClick3} checkable={true} checked={checked3} type={'blue'} shallow={true} >Musics</Tag>
              <Tag onTagClick={handleTagClick4} checkable={true} checked={checked4} type={'blue'} shallow={true} >Sports</Tag>
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code6}
          >
            <div style={{width: '100%'}} className="flex-column-wrapper">
              <div className={'padding-bottom-small'}>
                <Tag type={'blue'} closable={true}></Tag>
                <Tag type={'red'} closable={true}></Tag>
                <Tag type={'yellow'} closable={true}></Tag>
              </div>
              <div>
                <Tag size={'small'} type={'blue'} closable={true}></Tag>
                <Tag size={'small'} type={'red'} closable={true}></Tag>
                <Tag size={'small'} type={'yellow'} closable={true}></Tag>
              </div>

            </div>
          </Card>
        </div>
        <h3 className={'display-tittle'}>API</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource}/>
        </div>

      </div>
  )
}

export default TagDisplay
