import React from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import Select from "../../components/Select/select";
const options =  [{
  value: '',
  label: ''
}, {
  value: '',
  label: ''
}, {
  value: '',
  label: ''
}, {
  value: '',
  label: ''
}, {
  value: '',
  label: ''
}
]

const options2 =  [{
  value: '',
  label: ''
}, {
  value: '',
  label: '',
  disabled: true
}, {
  value: '',
  label: ''
}, {
  value: '',
  label: ''
}, {
  value: '',
  label: ''
}
]
const columns = [
  {
    title: '',
    dataIndex: 'Attribute',
    key: 'Attribute',

  },
  {
    title: '',
    dataIndex: 'Description',
    key: 'Description'
  },
  {
    title: '',
    dataIndex: 'Type',
    key: 'Type'
  },{
    title: '',
    dataIndex: 'Default',
    key: 'Default'
  }
];
const dataSource =  [
  {
    key: '1',
    attribute: 'options',
    description: '',
    type: `OptionProps[]`,
    default: ``,
  },
  {
    key: '2',
    attribute: 'defaultValue',
    description: '1',
    type: `string[]`,
    default: ``,
  },
  {
    key: '3',
    attribute: 'placeholder',
    description: '',
    type: 'string',
    default: '',
  },
  {
    key: '4',
    attribute: 'clearable',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '5',
    attribute: 'disabled',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '6',
    attribute: 'multiple',
    description: '',
    type: 'boolean',
    default: 'false',
  },
  {
    key: '7',
    attribute: 'onChange',
    description: '',
    type: '(selectedValue?: string, selectedValues?: string[]) => void',
    default: '',
  },
  {
    key: '8',
    attribute: 'onVisible',
    description: '',
    type: '(visible: boolean) => void',
    default: '',
  },
  {
    key: '9',
    attribute: 'zIndex',
    description: 'z-index',
    type: 'number',
    default: '10',
  },
  {
    key: '10',
    attribute: 'changeOnEnd',
    description: '',
    type: 'boolean',
    default: 'false',
  }
]
const dataSource2 =  [
  {
    key: '1',
    attribute: 'key',
    description: '',
    type: 'number',
    default: ``,
  },
  {
    key: '2',
    attribute: 'value',
    description: '',
    type: `string`,
    default: ``,
  },
  {
    key: '3',
    attribute: 'label',
    description: '',
    type: `string`,
    default: ``,
  },
  {
    key: '4',
    attribute: 'disabled',
    description: '',
    type: `boolean`,
    default: `false`,
  }
]
const Code1 = `<Select
    defaultValue={['']}
    zIndex={100}
    options={options}
    placeholder={''}
/>
`
const Code2 = `<Select
    defaultValue={['']}
    zIndex={80}
    clearable={true}
    options={options2}
    placeholder={''}
/>
`
const Code3 = `<Select
    disabled={true}
    options={options}
    placeholder={'disabled'}
/>
`
const Code4 = `<Select
    clearable={true}
    multiple={true}
    options={options}
    placeholder={''}
/>
`

const SelectDisplay = () => {


  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>Select </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}>,</p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code1}
          >
            <div style={{width: '250px'}}>
              <Select
                  defaultValue={['']}
                  zIndex={100}
                  options={options}
                  placeholder={''}
              />
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={'/'}
              explain={''}
              code={Code2}
          >
            <div style={{width: '250px'}}>
            <Select
                defaultValue={['']}
                zIndex={80}
                clearable={true}
                options={options2}
                placeholder={''}
            />
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code3}
          >
            <div style={{width: '250px'}}>
            <Select
                disabled={true}
                options={options}
                placeholder={'disabled'}
            />
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code4}
          >
            <div style={{width: '250px'}}>
            <Select
                clearable={true}
                multiple={true}
                options={options}
                placeholder={''}
            />
            </div>
          </Card>
        </div>

        <h3 className={'display-tittle'}>API</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource}/>
        </div>
        <h3 className={'display-tittle'}>Option</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource2}/>
        </div>

      </div>
  )
}

export default SelectDisplay
