import React from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import MenuItem from '../../components/Menu/menuItem'
import Menu from '../../components/Menu/menu'
import SubMenu from '../../components/Menu/subMenu'


const MenuDisplay = () => {
  const columns = [
    {
      title: '',
      dataIndex: 'Attribute',
      key: 'Attribute',

    },
    {
      title: '',
      dataIndex: 'Description',
      key: 'Description'
    },
    {
      title: '',
      dataIndex: 'Type',
      key: 'Type'
    },{
      title: '',
      dataIndex: 'Default',
      key: 'Default'
    }
  ];
  const dataSource1 =  [
    {
      key: '1',
      attribute: 'defaultIndex',
      description: '',
      type: `string`,
      default: `0`,
    },
    {
      key: '2',
      attribute: 'mode',
      description: '',
      type: `'horizontal' | 'vertical'`,
      default: `'horizontal'`,
    },
    {
      key: '3',
      attribute: 'defaultOpenSubMenus',
      description: '',
      type: 'string[]',
      default: '[]',
    },
    {
      key: '4',
      attribute: 'onSelect',
      description: '',
      type: '(selectedIndex: string) => void',
      default: '',
    },
    {
      key: '5',
      attribute: 'className',
      description: ' Menu ',
      type: 'string',
      default: '',
    },
    {
      key: '6',
      attribute: 'style',
      description: ' Menu ',
      type: 'React.CSSProperties',
      default: '',
    }
  ]
  const dataSource2 =  [
    {
      key: '1',
      attribute: 'index',
      description: '',
      type: `string`,
      default: ``,
    },
    {
      key: '2',
      attribute: 'disabled',
      description: '',
      type: `boolean`,
      default: `false`,
    },
    {
      key: '3',
      attribute: 'className',
      description: ' MenuItem ',
      type: 'string',
      default: '',
    },
    {
      key: '4',
      attribute: 'style',
      description: ' MenuItem ',
      type: 'React.CSSProperties',
      default: '',
    }
  ]
  const dataSource3 =  [
    {
      key: '1',
      attribute: 'tabKey',
      description: '',
      type: `string`,
      default: ``,
    },
    {
      key: '2',
      attribute: 'tab',
      description: '',
      type: `string`,
      default: ``,
    },
    {
      key: '3',
      attribute: 'index',
      description: '',
      type: 'number',
      default: '',
    }
  ]

  const Code1 = `<Menu>
  <MenuItem></MenuItem>
  <MenuItem></MenuItem>
  <MenuItem></MenuItem>
  <SubMenu tittle={''}>
    <MenuItem></MenuItem>
    <MenuItem></MenuItem>
    <MenuItem></MenuItem>
    <MenuItem></MenuItem>
  </SubMenu>
  <MenuItem disabled={true}></MenuItem>
</Menu>`
  const Code2 = `<Menu mode={'vertical'} defaultOpenSubMenus={['3']}>
  <MenuItem></MenuItem>
  <MenuItem></MenuItem>
  <MenuItem></MenuItem>
  <SubMenu tittle={''}>
    <MenuItem></MenuItem>
    <MenuItem></MenuItem>
    <MenuItem></MenuItem>
    <MenuItem></MenuItem>
  </SubMenu>
</Menu>`

  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>Menu </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code1}
          >
            <Menu>
              <MenuItem></MenuItem>
              <MenuItem></MenuItem>
              <MenuItem></MenuItem>
              <SubMenu tittle={''}>
                <MenuItem></MenuItem>
                <MenuItem></MenuItem>
                <MenuItem></MenuItem>
                <MenuItem></MenuItem>
              </SubMenu>
              <MenuItem disabled={true}></MenuItem>
            </Menu>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code2}
          >
            <Menu mode={'vertical'} defaultOpenSubMenus={['3']}>
              <MenuItem></MenuItem>
              <MenuItem></MenuItem>
              <MenuItem></MenuItem>
              <SubMenu tittle={''}>
                <MenuItem></MenuItem>
                <MenuItem></MenuItem>
                <MenuItem></MenuItem>
                <MenuItem></MenuItem>
              </SubMenu>
            </Menu>
          </Card>
        </div>
        <h3 className={'display-tittle'}>API</h3>
        <h3 className={'display-tittle'}>Menu</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource1}/>
        </div>
        <h3 className={'display-tittle'}>MenuItem</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource2}/>
        </div>
        <h3 className={'display-tittle'}>SubMenu</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource3}/>
        </div>
      </div>
  )
}

export default MenuDisplay
