import React, {useState} from 'react'
import Card from '../../components/Card/card'
import Table from "../../components/Table/table"
import Loading from "../../components/Loading/loading";
import Button from "../../components/Button/button";

const columns = [
  {
    title: '',
    dataIndex: 'Attribute',
    key: 'Attribute',

  },
  {
    title: '',
    dataIndex: 'Description',
    key: 'Description'
  },
  {
    title: '',
    dataIndex: 'Type',
    key: 'Type'
  },{
    title: '',
    dataIndex: 'Default',
    key: 'Default'
  }
];
const dataSource =  [
  {
    key: '1',
    attribute: 'loading',
    description: '',
    type: `boolean`,
    default: `false`,
  },
  {
    key: '2',
    attribute: 'tip',
    description: '',
    type: `string`,
    default: ``,
  },
  {
    key: '3',
    attribute: 'delay',
    description: 'ms',
    type: '',
    default: '0number',
  },
  {
    key: '4',
    attribute: 'color',
    description: '',
    type: 'string',
    default: '#84d8ff',
  }
]
const Code1 = `<Loading loading={true}>
`
const Code2 = `<Loading loading={loading}>
  <div style={{
    height: '180px', 
    width: '265px', 
    backgroundColor: '#f7f7f7', 
    borderRadius: '8px'
  }}>
  </div>
</Loading>
`
const Code3 = `<Loading loading={true} tip={'...'}>
  <div style={{
    height: '180px', 
    width: '265px', 
    backgroundColor: '#f7f7f7', 
    borderRadius: '8px'
  }}>
  </div>
</Loading>
`
const Code4 = `<Loading  tip={'...'} delay={500} loading={loadingDelay}>
  <div style={{
    height: '180px', 
    width: '265px', 
    backgroundColor: '#f7f7f7', 
    borderRadius: '8px'}}>
  </div>
</Loading>
`
const Code5 = `<Loading color={'#ff4b4b'} loading={true} tip={'...'}>
  <div style={{
    height: '180px', 
    width: '265px', 
    backgroundColor: '#f7f7f7', 
    borderRadius: '8px'}}>
  </div>
</Loading>
`
const LoadingDisplay = () => {
  const [loading, setLoading] = useState(false)
  const [loadingDelay, setLoadingDelay] = useState(false)
  const handleClick = () => {
    setLoading(!loading)
  }
  const handleClickDelay = () => {
    setLoadingDelay(!loadingDelay)
  }


  return (
      <div className={'display'}>
        <h2 className={'display-tittle'}>Loading </h2>
        <p className={'display-explain'}></p>
        <h3 className={'display-tittle'}></h3>
        <p className={'display-explain'}>,</p>
        <h3 className={'display-tittle'}></h3>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code1}
          >
            <Loading loading={true}>
            <div style={{height: '45px', width: '125px'}}>

            </div>
            </Loading>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code2}
          >
            <div>
              <div className="padding-bottom-small">
                <Loading loading={loading}>
                  <div style={{
                    height: '180px',
                    width: '265px',
                    backgroundColor: '#f7f7f7',
                    borderRadius: '8px'
                  }}>
                  </div>
                </Loading>
              </div>
              <Button size={'tiny'} onClick={handleClick}></Button>
            </div>

          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code3}
          >

            <Loading loading={true} tip={'...'}>
              <div style={{
                height: '180px',
                width: '265px',
                backgroundColor: '#f7f7f7',
                borderRadius: '8px'
              }}>
              </div>
            </Loading>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code4}
          >

            <div>
              <div className="padding-bottom-small">
                <Loading  tip={'...'} delay={500} loading={loadingDelay}>
                  <div style={{
                    height: '180px',
                    width: '265px',
                    backgroundColor: '#f7f7f7',
                    borderRadius: '8px'}}>
                  </div>
                </Loading>
              </div>
              <Button size={'tiny'} onClick={handleClickDelay}></Button>
            </div>
          </Card>
        </div>
        <div className="display-card-container">
          <Card
              divider={''}
              explain={''}
              code={Code5}
          >

            <Loading color={'#ff4b4b'} loading={true} tip={'...'}>
              <div style={{
                height: '180px',
                width: '265px',
                backgroundColor: '#f7f7f7',
                borderRadius: '8px'}}>
              </div>
            </Loading>
          </Card>
        </div>
        <h3 className={'display-tittle'}>API</h3>
        <div className="display-table-container">
          <Table columns={columns} dataSource={dataSource}/>
        </div>

      </div>
  )
}

export default LoadingDisplay
